import {useDispatch, useSelector} from "react-redux";
import {
    Avatar,
    Button,
    CircularProgress,
    Container,
    Grid, List, ListItem, ListItemAvatar, ListItemText,
    Paper,
    Toolbar,
    Typography
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {updateWalletBalance, updateWalletCoineusInfo} from "../redux/slices/coineusSlice";

import cns_logo from "../images/logo192.png";
import fuse_logo from "../images/fuse_logo.png";

export default function HomePage() {

    const dispatch = useDispatch();

    const {wallet, prices} = useSelector(state => state.coineus);

    const CoineusContractAddress = '0xc2E299b47398963c618De5b05C6BdeCd4cc64022';

    const [pending, setPending] = useState(false);

    useEffect(() => {
        if (wallet.address) {
            getUserInfo();
        }
    }, [wallet.address])

    const getUserInfo = () => {
        const web3 = new Web3('https://rpc.fuse.io');


        web3.eth.getBalance(wallet.address).then(resp => {
            dispatch(updateWalletBalance(resp / (10 ** 18)))
        })

        const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);

        const calls = [
            contract.methods.balanceOf(wallet.address).call,
            contract.methods.getAccountDividendsInfo(wallet.address).call
        ];

        makeBatchRequest(web3, calls).then(resp => {
            let [
                balance,
                info
            ] = resp;

            let withdrawableDividends = info[3];
            let totalDividends = info[4];
            let secondsUntilAutoClaimAvailable = info[7];

            balance = balance / (10 ** 18);
            withdrawableDividends = withdrawableDividends / (10 ** 18);
            totalDividends = totalDividends / (10 ** 18);

            dispatch(updateWalletCoineusInfo({
                balance,
                withdrawableDividends,
                totalDividends,
                secondsUntilAutoClaimAvailable
            }));

        })
    }

    const claim = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const contract = new web3.eth.Contract(coineus_abi, CoineusContractAddress);

            contract.methods
                .claim()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getUserInfo();
                    setPending(false);
                });
        }
    }

    return (<Container maxWidth="sm">
        <Toolbar/>
        <Grid container spacing={2}>

            {
                !wallet.address && <Grid item xs={12}>
                    <Typography>Connect Wallet to see your details</Typography>
                </Grid>
            }
            <Grid item xs={12}>
                <Paper style={{padding: 20}}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Typography
                                variant="h4">{wallet.coineusInfo?.totalDividends.toLocaleString() || '--'} fUSD</Typography>
                            <Typography variant="caption">Total Dividends All-time</Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography
                                variant="h4">{wallet.coineusInfo?.withdrawableDividends.toLocaleString() || '--'} fUSD</Typography>
                            <Typography variant="caption">Claimable Dividends</Typography>
                        </Grid>
                        <Grid item xs={12}>
                            <Button onClick={claim} variant='contained' fullWidth
                                    disabled={!wallet.address || wallet.coineusInfo?.secondsUntilAutoClaimAvailable > 0}>Claim</Button>
                        </Grid>
                        {
                            wallet.coineusInfo?.secondsUntilAutoClaimAvailable > 0 && <Grid item xs={12}>
                                <Typography>{wallet.coineusInfo?.secondsUntilAutoClaimAvailable} Seconds until claim
                                    available</Typography>
                            </Grid>
                        }
                    </Grid>
                </Paper>
            </Grid>
            <Grid item xs={12}>
                {
                    pending && <><CircularProgress size={20}/><Typography variant='caption' style={{
                        verticalAlign: 'top',
                        marginLeft: 10
                    }}>Transaction Pending</Typography></>
                }
            </Grid>
            <Grid item xs={12}>
                <List>
                    <Paper>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar src={fuse_logo}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary='Fuse'
                                secondary={`$${prices.fuse?.inUSD.toLocaleString() || '--'}`}
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={wallet.balance?.toLocaleString() || '--'}
                                secondary={`$${wallet.balance ? (prices.fuse?.inUSD * wallet.balance).toLocaleString() : '--'}`}
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </Paper>
                </List>

                <List>
                    <Paper>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar src={cns_logo}/>
                            </ListItemAvatar>
                            <ListItemText
                                primary='CNS'
                                secondary={`$${prices.cns?.inUSD.toLocaleString() || '--'}`}
                                primaryTypographyProps={{fontWeight: 700}}
                            />
                            <ListItemText
                                primary={wallet.coineusInfo?.balance.toLocaleString() || '--'}
                                secondary={`$${wallet.coineusInfo?.balance ? (prices.cns?.inUSD * wallet.coineusInfo?.balance).toLocaleString() : '--'}`}
                                primaryTypographyProps={{textAlign: 'right', fontWeight: 700}}
                                secondaryTypographyProps={{textAlign: 'right'}}
                            />
                        </ListItem>
                    </Paper>
                </List>
            </Grid>
        </Grid>
    </Container>);
}